* {
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

.body {
  background-color: #f3f3f3;
  width: 100vw;
  height: 100vh;
}

.root {
  width: 100%;
  height: 100%;
}

a, .github-link {
  all: unset;
  cursor: pointer;
}

.main-header {
  background-color: #fff;
  border-bottom: 1px solid gray;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  padding: 5px 70px;
  display: flex;
  position: sticky;
  top: 0;
}

.main-header h1 {
  color: teal;
  cursor: pointer;
  height: 57px;
  font-family: Lobster, sans-serif;
  position: relative;
}

.main-header h1:after {
  content: "made width heart";
  font-family: Poppins, sans-serif;
  font-size: 10px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.main-header ul {
  justify-content: space-between;
  align-items: center;
  width: 40vw;
  margin: 0 5px;
  padding: 10px;
  font-family: Poppins, sans-serif;
  font-size: 15px;
  font-weight: 500;
  list-style-type: none;
  display: flex;
}

.main-header ul li {
  cursor: pointer;
}

.body-container {
  background-color: #f3f3f3;
  grid-template-rows: 40px 1fr;
  margin-top: 20px;
  display: grid;
}

.fullpage {
  background-color: #f3f3f3;
  min-height: 100vh;
}

.card {
  cursor: pointer;
  border-radius: 7px;
  grid-template-rows: 200px 75px;
  grid-template-columns: 50% 50%;
  align-content: space-around;
  width: 17vw;
  height: 300px;
  margin: 5px;
  padding: 7px;
  transition: all .2s;
  display: grid;
}

.card:hover {
  box-shadow: 0 0 7px .1px #000;
}

.card-photo {
  object-fit: cover;
  border-radius: 7px;
  grid-column: 1 / 3;
  place-self: flex-start center;
  width: 100%;
  height: 100%;
  display: flex;
}

.card-info-right {
  text-align: end;
}

.card-container {
  flex-wrap: wrap;
  flex-shrink: 0px;
  justify-content: start;
  gap: 10px;
  height: 100%;
  padding: 0 140px;
  display: flex;
}

.card-info-right p:first-child {
  color: #fff;
  background-color: #267e3e;
  border-radius: 4px;
  padding: 1px 3px;
  font-size: 12px;
  font-weight: 600;
  display: inline;
}

.card-subinfo {
  color: gray;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  font-size: 13px;
  font-weight: 600;
  display: -webkit-box;
  overflow: hidden;
}

h4 {
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  display: -webkit-box;
  overflow: hidden;
}

.rating-btn, .search-btn {
  color: #fff;
  box-sizing: border-box;
  background-color: #43766c;
  border: 2px solid #00b3b3;
  border-top: none;
  border-bottom-width: 4px;
  border-radius: 5px;
  justify-self: center;
  width: 7em;
  margin: 0 5px;
  padding: 5px;
  font-weight: 600;
}

.rating-btn:hover, .search-btn:hover {
  background-color: #428679;
}

.rating-btn:active, .search-btn:active {
  border: 1px solid #00b3b3;
}

.shimmer-card-container {
  flex-wrap: wrap;
  margin: 7vh 0 0 10vw;
  display: flex;
}

.shimmer-card {
  border-radius: 7px;
  grid-template-rows: 200px 75px;
  align-content: space-around;
  width: 17vw;
  height: 300px;
  margin: 15px;
  padding: 7px;
  display: grid;
}

.shimmer-photo {
  background-color: #969494;
  border-radius: 7px;
  width: 90%;
}

.shimmer-heading {
  background-color: #969494;
  border-radius: 7px;
  width: 120px;
  height: 10px;
}

.shimmer-subheading {
  background-color: #969494;
  border-radius: 7px;
  width: 50px;
  height: 10px;
  margin-top: 5px;
}

.login-btn {
  color: #fff;
  background-color: #43766c;
  border-top: none;
  border-bottom: 4px solid #00b3b3;
  border-left: 4px solid #00b3b3;
  border-right: none;
  border-radius: 5px;
  width: 15%;
  height: 4em;
}

.login-btn:active {
  border: 1px solid #00b3b3;
}

.search-input {
  border: 2px solid #43766c;
  border-radius: 5px;
  padding: 5px;
}

.body-btn-container {
  padding: 0 140px;
  display: flex;
}

button {
  cursor: pointer;
}

.about-card-container {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 90vh;
  display: flex;
}

.about-card {
  width: 70%;
  height: 70vh;
}

.about-heading {
  text-align: center;
}

